<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <span>法币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="listQuery.coinBaseId"
                       placeholder="全部类型">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <span>交易币类型</span>
            <el-select clearable style="width: 150px" class="filter-item" v-model="listQuery.coinTradeId"
                       placeholder="全部类型">
                <el-option v-for="status in coinList" :key="status.baseSeq" :label="status.chineseName"
                           :value="status.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="handleFilter" size="small" style="margin-left: 30px">查询</el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间"
                    style="width: 100%; ">
                <el-table-column type="index" label="序号" width="80"></el-table-column>
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>
            <div v-show="!listLoading" class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page"
                               :page-sizes="[15,30,45,60]" :page-size="listQuery.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {tradehistory} from '@/api/useraddress';
    import {getgoodslist} from '@/api/awaits';
    import calc from "@/utils/calculate-big-num";

    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    export default {
        data() {
            return {
                allCoin,
                //总条数
                total: null,
                //返回来列表的数据
                tableData: [],
                coinList: [],
                goodsall: {
                    types: [1]
                },
                listLoading: true,
                listQuery: {
                    coinBaseId: '',
                    coinTradeId: '',
                    page: 1,
                    pageSize: 15
                },
                tableHeader: [
                    {
                        label: "法币",
                        prop: "coinBaseString",
                    }, {
                        label: "交易资产",
                        prop: "coinTradeString",
                    }, {
                        label: "收盘价",
                        prop: "historyPrice",
                        formatter(row, column, value) {
                            return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                        }
                    }, /*{
                        label: "操作内容",
                        prop: "actionContent",
                    },*/ {
                        label: "时间",
                        prop: "createTime",
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }
                ]
            }

        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                tradehistory(this.listQuery).then(response => {
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                        this.listLoading = false;
                    } else {
                        this.tableData = response.data.list;
                        this.total = response.data.total;
                        this.listLoading = false;
                    }
                })
            },
            /**
             * 条件筛选查询对象
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.pageSize = val;
                this.getList();
            },
            getgoods() {
                getgoodslist(this.goodsall).then(response => {
                    this.coinList = response.data;
                    this.coinList.unshift(this.allCoin);
                })
            }

        },
        created() {
            this.getgoods();
            this.getList();
        }
    }
</script>



