import fetch from '@/utils/fetch'

//搜索所有提现地址
export function getrechargelist(query) {
    return fetch({
        url: 'admin/property/withdraw',
        method: 'get',
        params: query
    })
}

//搜索所有充值
export function getwithdrawlist(query) {
    return fetch({
        url: 'admin/property/trecharge',
        method: 'get',
        params: query
    })
}

//搜索所有提货地址
export function getgoodslist(query) {
    return fetch({
        url: 'admin/property/goods',
        method: 'get',
        params: query
    })
}

//搜索所有提货地址
export function currencyall(query) {
    return fetch({
        url: 'admin/currencywallet',
        method: 'get',
        params: query
    })
}//搜索所有提货地址
export function useractionlog(query) {
    return fetch({
        url: '/admin/useractionlog/page',
        method: 'post',
        data: query
    })
}
export function tradehistory(query) {
    return fetch({
        url: '/admin/tradehistory',
        method: 'get',
        params: query
    })
}
export function introlinfolist(query) {
    return fetch({
        url: '/admin/introlinfolist',
        method: 'get',
        params: query
    })
}




